.greetingForm {
    width: 100vw;
    height: 200px;
    padding: 20px 80px;
    border: 1px solid rgb(0, 0, 0, 0.6);
}

.inputAndButtonContainer {
    display: flex;
    padding: 20px 20px;
}

.error {
    color: red;
    font-size: 14px;
}

.input {
    
}

.input:focus {
}

.errorInput {
    margin-right: 20px;
    padding: 5px 10px;
    height: 30px;
    width: 250px;
    outline: none;
    border: 1px solid rgb(0, 0, 0, 0.6);
    border-radius: 5px;
}

.button {
    background-color: rgb(34, 74, 218);
    color: #fff;
    height: 30px;
    font-size: 15px;
    font-weight: 500;
    padding: 5px 15px;
    border-radius: 5px;
    cursor: pointer;
    border: transparent;
}

.button:focus {
    background-color: #fff;
    color: rgb(34, 74, 218);
    border: 1px solid rgb(34, 74, 218);
}

.button:active {
    /* background-color: #fff;
    color: blue;
    border: 1px solid blue; */
}

.button:disabled {
    filter: brightness(70%);
}

.text {
    color: rgb(0, 0, 0, 0.4);
}

.greeting {
    font-size: 18px;
    font-weight: 400;
}
